<style>
.home-slider {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: flex-end;
  min-height: 480px;
  position: relative;
}
.home-slider-top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-right: 96px;
  width: 100%
}
.home-slider-title {
  font-size: 14px;
  font-weight: 800;
  line-height: 1;
  margin: 0;
  text-align: center;
  display: inline-flex;
  align-items: center;
  color: #ffffff;
}
.link-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background: #ffb81e;
  border: none;
  border-radius: 40px;
  box-shadow: transparent;
  color: #000c1e;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: normal;
  line-height: 1.2;
  min-height: 40px;
  min-width: 100px;
  margin: 0;
  padding: 0 24px;
  outline: none;
  text-decoration: none;
  text-shadow: none;
  vertical-align: middle;
  position: relative;
  transition: background-color .3s ease, color .3s ease, box-shadow .3s ease;

}
.link-btn:hover {
  background: #ffb81e;
  box-shadow: 0 0 40px rgba(255, 184, 30, .3);
}
.see-more-btn {
  min-height: 40px;
  min-width: 112px;
}
.link-btn-secondary {
  background: #00224b;
  box-shadow: none;
  color: #ffffff;
}
.game {
  background-color: #00224b;
  border-radius: 8px;
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.game-top, .game-bottom {
  display: flex;
  align-items: center;
}
.game-top {
  flex-direction: column;
  height: 95px;
  position: relative;
}
.game-bottom {
  height: 25px;
}
.game-block-img {
  display: flex;
  font-size: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.game-img {
  width: 100%;
  border: none;
  object-fit: cover;
  transform: scale(1);
  transition: transform .3s ease;
}
.game-action {
  background-color: rgba(0,12,30,.5);
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  padding: 5px;
  transition: opacity .3s ease;
}
.game-btns, .game-provider {
  display: block;
  padding: 0;
  text-align: center;
  width: 100%;
}
.btn {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background: #ffb81e;
  border: none;
  border-radius: 40px;
  box-shadow: transparent;
  color: #000c1e;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: normal;
  line-height: 1.2;
  margin: 0;
  padding: 0 24px;
  min-height: 40px;
  min-width: 100px;
  outline: none;
  text-decoration: none;
  text-shadow: none;
  position: relative;
  vertical-align: middle;
  transition: background-color .3s ease, color .3s ease, box-shadow .3s ease;
}
.btn:active, .btn:focus,
.btn:hover,
.link-btn:active,
.link-btn:focus,
.link-btn:hover {
  color: #000c1e;
  outline: none;
  text-decoration: none;
}
.btn, .btn:not(:disabled) {
  cursor: pointer;
}
.btn:hover {
  background: #ffb81e;
  box-shadow: 0 0 40px rgba(255, 184, 30, .3)
}
.game .game-btn-play {
  bottom: 0;
  font-weight: 900;
  width: 100%;
  min-height: 40px;
  min-width: 70px;
  padding: 0 22px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.game .game-title {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: .16px;
  text-align: center;
  width: 100%;
  height: 100%;
  max-height: 2.4em;
  margin: 0;
  padding: 0 10px;
}
.game .game-title>a[href] {
  color: #ffffff;
  margin: auto;
  padding: 0;
  text-decoration: none;
}
.slider-top .swiper-button-next, .slider-top .swiper-button-prev {
  background-color: #00224b;
  display: inline-flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  height: 40px;
  width: 40px;
  margin: 0;
  padding: 0;
  outline: none;
  top: 0px;
  transition: background-color .3s ease, color .3s ease;
  opacity: 1;
}
.slider-top .swiper-button-next:hover, .slider-top .swiper-button-prev:hover {
  background-color: #ffb81e;
  color: #000c1e;
}
.slider-top .swiper-button-next:after, .slider-top .swiper-button-prev:after {
  font-size: 14px;
  font-weight: 900;
}
.slider-top .swiper-button-prev {
  border-radius: 20px 0 0 20px;
  right: 40px;
  left: auto;
}
.slider-top .swiper-button-next {
  border-radius: 0 20px 20px 0;
  right: 0px;
}
@media screen and (min-width: 1248px) {
  .game .game-btn-play {
    position: static;
    width: auto;
  }
  .game:hover .game-action {
    opacity: 1;
  }
}
</style>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/navigation'
import { Navigation } from 'swiper'
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup () {
    return {
      modules: [Navigation]
    }
  }
}
</script>
