<style>
.home-filter {
  padding: 30px 0 40px;
}
.games-filter {
  display: block;
  z-index: 100;
}
.games-filter, .games-filter-grid {
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
}
.games-filter-grid {
  display: grid;
  grid-column-gap: 16px;
  grid-row-gap: 30px;
  grid-template-columns: 1fr auto;
}
.games-filter-cell {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  width: 100%;
}
.search {
  grid-column: 1;
  grid-row: 2;
}
.search-filter {
  width: 100%;
  background-color: #00224b;
  border-radius: 20px;
  display: block;
  margin: 0;
  padding-left: 20px;
  position: relative;
}
.search-filter .search-filter-btn {
  height: 47px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  background: transparent none;
  border: none;
  box-shadow: none;
  color: #fff;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.2;
  text-align: left;
  text-shadow: none;
  vertical-align: middle;
  outline: none;
  margin: 0;
  padding: 0;
  transition: color .3s ease;
}
.home-filter .search-filter .search-filter-btn {
  height: 40px;
  width: 100%;
}
.search-filter-icon {
  color: #fff;
  display: inline-flex;
  font-size: 16px;
  left: 0;
  pointer-events: none;
}
.search-filter-close, .search-filter-icon {
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 100%;
  position: absolute;
  top: 0;
}
.search-filter .search-filter-btn .search-filter-icon {
  color: inherit;
  margin: 0 16px 0 0;
  position: relative;
  width: auto;
}
.category {
  grid-column: span 2;
  grid-row: 1;
}
.games-filter-category {
  display: flex;
  align-items: center;
  align-self: stretch;
  width: 100%;
  margin: 0;
  padding: 0;
}
.game-category-menu {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 22px 16px;
  width: 100%;
}
.game-category-menu-item {
  flex-wrap: wrap;
  align-self: stretch;
}
.game-category-menu-item, .game-category-menu-link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin: 0;
  width: auto;
}
.game-category-menu-link {
  color: #ffffff;
  flex-wrap: nowrap;
  padding: 0;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: .12px;
  line-height: 1.2;
  outline: none;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  transition: color .3s ease;
}
.game-category-menu-icon {
  margin-right: 8px;
  transition:  color .3s ease;
}
.game-category-menu-name {
  display: inline-flex;
  justify-content: center;
  position: relative;
}
.game-category-menu-name:after {
  content: '';
  background-color: #ffb81e;
  width: 0;
  position: absolute;
  bottom: -4px;
  display: block;
  height: 3px;
  transition: width .3s ease;
}
.game-category-menu-link.active .game-category-menu-name:after,
.game-category-menu-link:active .game-category-menu-name:after,
.game-category-menu-link:focus .game-category-menu-name:after,
.game-category-menu-link:hover .game-category-menu-name:after {
  width: 100%;
}
.game-category-menu-link.active .game-category-menu-icon,
.game-category-menu-link:active .game-category-menu-icon,
.game-category-menu-link:focus .game-category-menu-icon,
.game-category-menu-link:hover .game-category-menu-icon {
  fill: #ffb81e;
}
.providers {
  grid-column: 2;
  grid-row: 2;
}
.menu-providers-select {
  color: #ffffff;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: .12px;
  line-height: 1.2;
  padding: 0;
  text-align: left;
}
.menu-providers-select, .menu-providers-select-btn {
  display: flex;
  align-items: center;
  align-self: stretch;
  justify-content: center;
  flex-direction: row;
  margin: 0;
  width: 100%;
}
.menu-providers-select-btn {
  background-color: transparent;
  border-radius: 4px;
  flex-wrap: nowrap;
  min-height: 40px;
  cursor: pointer;
}
.menu-providers-select-caret {
  width: auto;
  display: inline-block;
  font-size: 6px;
  vertical-align: top;
  margin: 0 0 0 6px;
  /* transform: rotate(0); */
  cursor: pointer;
  transition: transform .3s ease;
}
.open {
  transform: rotate(180deg);
}
.menu-providers-select-content.show {
  display: block;
}
.menu-providers-select-content {
  background-color: #00224b;
  border-radius: 8px;
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 11;
  margin: 8px 0 0;
  padding: 8px 4px;
  width: 100%;
}
.menu-providers-select-list {
  display: flex;
  flex-wrap: wrap;
  column-count: 1;
  gap: 10px 32px;
  margin: 0;
  padding: 8px 15px;
  text-align: center;
  width: 100%;
}
.menu-providers-select-item {
  display: inline-flex;
  align-items: center;
  line-height: 1;
  margin: 0;
  vertical-align: top;
  width: 100%;
}
.menu-providers-select-link {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  flex-wrap: nowrap;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: .12px;
  line-height: 1.2;
  max-width: 100%;
  min-height: 16px;
  width: auto;
  margin: 0;
  padding: 0;
  text-align: left;
  text-decoration: none;
  text-transform: uppercase;
  transition: color .3s ease;
}
.menu-providers-icon-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 32px;
  width: 32px;
  min-width: 32px;
  margin-right: 16px;
}
.menu-providers-icon-image {
  max-height: 100%;
  max-width: 100%;
}
@media screen and (min-width: 768px) {
  .menu-providers-select-list {
    column-count: 2;
  }
}
@media screen and (min-width: 960px) {
  .game-category-menu {
    justify-content: flex-start;
  }
  .menu-providers-select-list {
    column-count: 3;
  }
}
@media screen and (min-width: 1248px) {
  .games-filter-grid {
    grid-template-columns: 1fr 200px auto;
  }
  .search {
    grid-column: 2;
    grid-row: 1;
  }
  .category {
    grid-column: 1;
    grid-row: span 2;
  }
  .providers {
    grid-column: 3;
    grid-row: 1;
  }
  .games-filter-category {
    align-items: flex-start;
    padding-top: 8px;
  }
  .menu-providers-select-content {
    top: 40px;
  }
  .menu-providers-select-list {
    column-count: 4;
  }
}
@media screen and (min-width: 1632px) {
  .menu-providers-select-list {
    column-count: 5;
  }
}
</style>

<script>
export default {
  methods: {
    showProidersMenu () {
      this.$refs.menuProvidersCaret.classList.toggle('open')
      this.$refs.menuProvidersContent.classList.toggle('show')
    }
  }
}
</script>
